___CSS_LOADER_EXPORT___.push([module.id, "\n.doc-search {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  padding: 1rem;\n}\n.doc-search__results {\n  flex: 1 1 auto;\n  overflow-y: auto;\n  height: 0;\n}\n.doc-search__results .snippet-card:focus {\n  border-color: var(--bs-primary);\n}\n", "",{"version":3,"sources":["webpack://./peachjam/js/components/DocumentSearch/index.vue"],"names":[],"mappings":";AA4IA;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ,aAAa;AACf;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,SAAS;AACX;AAEA;EACE,+BAA+B;AACjC","sourcesContent":["<template>\n  <div class=\"doc-search\">\n    <form\n      class=\"doc-search__form mb-2\"\n      @submit.prevent=\"() => (q = $refs.q.value)\"\n    >\n      <div class=\"input-group\">\n        <input\n          ref=\"q\"\n          type=\"text\"\n          required\n          class=\"form-control\"\n          :placeholder=\"$t('Search document content')\"\n          :aria-label=\"$t('Search document content')\"\n          aria-describedby=\"search-content-button\"\n          minlength=\"3\"\n        >\n        <button\n          class=\"btn btn-secondary\"\n          type=\"submit\"\n        >\n          {{ $t(\"Search\") }}\n        </button>\n      </div>\n      <div class=\"text-end mt-2\">\n        <HelpBtn page=\"search/search-inside-a-document\" />\n        <a\n          v-if=\"marks.length\"\n          href=\"#\"\n          @click.prevent=\"clear\"\n        >{{ $t(\"Clear\") }}</a>\n      </div>\n      <div\n        v-if=\"!marks.length && q\"\n        class=\"mt-2\"\n      >\n        {{ $t(\"No results\") }}\n      </div>\n    </form>\n    <div class=\"doc-search__results\">\n      <div v-if=\"marks.length\">\n        <AknSnippets\n          v-if=\"docType === 'akn'\"\n          :nodes=\"marks\"\n          @go-to-snippet=\"goToSnippet\"\n        />\n        <HTMLSnippets\n          v-if=\"docType === 'html'\"\n          :nodes=\"marks\"\n          @go-to-snippet=\"goToSnippet\"\n        />\n\n        <PdfSnippets\n          v-if=\"docType === 'pdf'\"\n          :nodes=\"marks\"\n          @go-to-snippet=\"goToSnippet\"\n        />\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport Mark from 'mark.js';\nimport HTMLSnippets from './HTMLSnippets.vue';\nimport PdfSnippets from './PdfSnippets.vue';\nimport AknSnippets from './AknSnippets.vue';\nimport { scrollToElement } from '../../utils/function';\nimport HelpBtn from \"../HelpBtn.vue\";\n\nexport default {\n  name: 'DocumentSearch',\n  components: {HelpBtn, AknSnippets, PdfSnippets, HTMLSnippets },\n  props: {\n    docType: {\n      type: String,\n      required: true,\n      validator (value) {\n        // The value must match one of these strings\n        return ['akn', 'pdf', 'html'].includes(value);\n      }\n    },\n    document: {\n      type: HTMLElement,\n      required: true\n    },\n    mountElement: {\n      type: HTMLElement,\n      required: true\n    }\n  },\n  data: () => ({\n    q: '',\n    marks: [],\n    markInstance: null\n  }),\n  watch: {\n    q (newValue) {\n      // Unmark when search if mark instance exists already\n      if (this.markInstance) {\n        this.markInstance.unmark();\n        this.marks = [];\n      }\n      this.searchDoc(newValue);\n    }\n  },\n  methods: {\n    clear () {\n      this.$refs.q.value = '';\n      this.q = '';\n    },\n    searchDoc (q) {\n      if (!this.markInstance) {\n        this.markInstance = new Mark(this.document);\n      }\n      this.markInstance.mark(q, {\n        separateWordSearch: false\n      });\n      this.marks = [...this.document.querySelectorAll('[data-markjs]')];\n    },\n\n    goToSnippet (node) {\n      this.mountElement.dispatchEvent(new CustomEvent('going-to-snippet'));\n      // Wait for offset canvas to close then scroll\n      window.setTimeout(() => {\n        scrollToElement(node, () => {\n          node.style.outline = '2px solid transparent';\n          node.style.transition = 'outline-color 400ms ease-in-out';\n          node.style.outlineColor = 'var(--bs-primary)';\n          window.setTimeout(() => {\n            node.style.outlineColor = 'transparent';\n          }, 400);\n        }, 60);\n      }, 300);\n    }\n  }\n};\n</script>\n\n<style>\n.doc-search {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  padding: 1rem;\n}\n\n.doc-search__results {\n  flex: 1 1 auto;\n  overflow-y: auto;\n  height: 0;\n}\n\n.doc-search__results .snippet-card:focus {\n  border-color: var(--bs-primary);\n}\n</style>\n"],"sourceRoot":""}]);
