___CSS_LOADER_EXPORT___.push([module.id, "\n.facets-scrollable[data-v-32034ea4] {\n  max-height: 25vh;\n  overflow-y: auto;\n}\n", "",{"version":3,"sources":["webpack://./peachjam/js/components/FilterFacets/SingleFacet.vue"],"names":[],"mappings":";AAoKA;EACE,gBAAgB;EAChB,gBAAgB;AAClB","sourcesContent":["<template>\n  <template v-if=\"facet.options && facet.options.length\">\n    <li class=\"list-group-item\">\n      <div class=\"d-flex justify-content-between mb-2\">\n        <strong>{{ facet.title }}</strong>\n        <div class=\"d-flex align-items-center\">\n          <a\n            v-if=\"showClearFilter\"\n            href=\"#\"\n            @click.prevent=\"$emit('clear-facet', facet.name)\"\n          >\n            {{ $t('Clear') }}\n          </a>\n\n          <span\n            v-if=\"loading\"\n            class=\"circle-loader ms-2\"\n          />\n        </div>\n      </div>\n      <div :class=\"`${facet.type === 'letter-radio' ? '' : 'facets-scrollable'}`\">\n        <template v-if=\"facet.type === 'checkboxes'\">\n          <div\n            v-for=\"(option, optIndex) in facet.options\"\n            :key=\"optIndex\"\n            class=\"d-flex justify-content-between align-items-center\"\n          >\n            <div class=\"form-check flex-grow-1\">\n              <input\n                :id=\"`${facet.name}_${optIndex}`\"\n                :value=\"option.value\"\n                class=\"form-check-input\"\n                type=\"checkbox\"\n                :name=\"facet.name\"\n                :checked=\"facet.value.some(value => String(value) === String(option.value))\"\n                @input=\"(e) => $emit('on-change', e, facet)\"\n              >\n              <label\n                class=\"form-check-label\"\n                :for=\"`${facet.name}_${optIndex}`\"\n              >\n                {{ option.label }}\n              </label>\n            </div>\n            <div>\n              <span class=\"badge bg-light text-dark\">{{ option.count }}</span>\n            </div>\n          </div>\n        </template>\n        <template v-if=\"facet.type === 'radio'\">\n          <div\n            v-for=\"(option, optIndex) in facet.options\"\n            :key=\"optIndex\"\n            class=\"d-flex justify-content-between align-items-center\"\n          >\n            <div class=\"form-check flex-grow-1\">\n              <input\n                :id=\"`${facet.name}_${optIndex}`\"\n                :checked=\"String(facet.value) === String(option.value)\"\n                :value=\"option.value\"\n                class=\"form-check-input\"\n                type=\"radio\"\n                :name=\"facet.name\"\n                @input=\"(e) => $emit('on-change',e, facet)\"\n              >\n              <label\n                class=\"form-check-label\"\n                :for=\"`${facet.name}_${optIndex}`\"\n              >\n                {{ option.label }}\n              </label>\n            </div>\n            <div>\n              <span class=\"badge bg-light text-dark\">\n                {{ option.count }}\n              </span>\n            </div>\n          </div>\n        </template>\n        <template v-if=\"facet.type === 'letter-radio'\">\n          <div class=\"letter-radiobox-container\">\n            <label\n              v-for=\"(option, optIndex) in facet.options\"\n              :key=\"optIndex\"\n              class=\"letter-radiobox\"\n            >\n              <input\n                :key=\"optIndex\"\n                :value=\"option.value\"\n                :checked=\"String(facet.value) === String(option.value)\"\n                type=\"radio\"\n                :name=\"facet.name\"\n                @input=\"(e) => $emit('on-change',e, facet)\"\n              >\n              <span class=\"letter-radiobox__text\">\n                {{ option.label }}\n              </span>\n            </label>\n          </div>\n        </template>\n      </div>\n    </li>\n  </template>\n  <template v-if=\"facet.type === 'boolean'\">\n    <div class=\"list-group-item d-flex justify-content-between\">\n      <div class=\"d-flex justify-content-between align-items-center\">\n        <div class=\"form-check\">\n          <input\n            :id=\"facet.name\"\n            :checked=\"facet.value\"\n            class=\"form-check-input\"\n            type=\"checkbox\"\n            :name=\"facet.name\"\n            @input=\"(e) => $emit('on-change', e, facet)\"\n          >\n          <label\n            class=\"form-check-label\"\n            :for=\"facet.name\"\n          >\n            <strong>{{ facet.title }}</strong>\n          </label>\n        </div>\n      </div>\n      <div class=\"d-flex align-items-center\">\n        <span class=\"badge bg-light text-dark\">\n          {{ facet.count }}\n        </span>\n        <span\n          v-if=\"loading\"\n          class=\"circle-loader ms-2\"\n        />\n      </div>\n    </div>\n  </template>\n</template>\n\n<script>\nexport default {\n  name: 'SingleFacet',\n  props: {\n    facet: {\n      type: Object,\n      required: true\n    },\n    loading: {\n      type: Boolean,\n      required: false,\n      default: false\n    }\n  },\n  emits: ['clear-facet', 'on-change'],\n  computed: {\n    showClearFilter () {\n      if (this.facet.type === 'checkboxes') {\n        return this.facet.value.length;\n      } else {\n        return this.facet.value;\n      }\n    }\n  }\n};\n</script>\n\n<style scoped>\n.facets-scrollable {\n  max-height: 25vh;\n  overflow-y: auto;\n}\n</style>\n"],"sourceRoot":""}]);
